.spinner {
  position: absolute;
  z-index: 1000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  margin: 0 auto;
  align-self: center;

  svg {
    align-self: center;
    width: 150px;
    height: 150px;
  }
}

.hidden {
  display: none;
}
